//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$mainFamily: 'PT Sans';
$poppins: 'Poppins';

@mixin fmain {
	font-family: $mainFamily, Arial, sans-serif; }
@mixin ftitle {
	font-family: $poppins, Arial, sans-serif; }

@font-face {
	@include fontdefault(normal, 400, $mainFamily);
	@include fontpath('PT Sans', 'PTSans-Regular', PTSans-Regular); }
@font-face {
	@include fontdefault(italic, 400, $mainFamily);
	@include fontpath('PT Sans Italic', 'PTSans-Italic', PTSans-Italic); }

@font-face {
	@include fontdefault(normal, 700, $mainFamily);
	@include fontpath('PT Sans Bold', 'PTSans-Bold', PTSans-Bold); }
@font-face {
	@include fontdefault(italic, 700, $mainFamily);
	@include fontpath('PT Sans Bold Italic', 'PTSans-BoldItalic', PTSans-BoldItalic); }



@font-face {
	@include fontdefault(normal, 700, $poppins);
	@include fontpath('Poppins-Bold', 'Poppins-Bold', Poppins-Bold); }
@font-face {
	@include fontdefault(italic, 700, $poppins);
	@include fontpath('Poppins Bold Italic', 'Poppins-BoldItalic', Poppins-BoldItalic); }

@font-face {
	@include fontdefault(normal, 800, $poppins);
	@include fontpath('Poppins Extra Bold', 'Poppins-ExtraBold', Poppins-ExtraBold); }
@font-face {
	@include fontdefault(italic, 800, $poppins);
	@include fontpath('Poppins Extra Bold alic', 'Poppins-ExtraBoldItalic', Poppins-ExtraBoldItalic); }
